.form {
  margin-top: 20px;
  background-image: url("../background.jpg");
  background-repeat: round;
  color: hsl(240, 67%, 15%);
  border-radius: 5px;
  padding: 15px;
}

.form__fieldsetInput {
  border: 1px solid hsl(240, 69%, 75%);
  padding: 5px;
  min-width: 230px;
  border-radius: 5px;
}

.form__fieldset {
  border: 1px solid hsl(240, 69%, 75%);
  padding: 20px;
  border-radius: 5px;
  margin: 15px;
}

.form__button {
  width: 100%;
  margin-bottom: 10px;
  background: transparent;
  border: 1px solid hsl(240, 67%, 75%);
  padding: 5px;
  border-radius: 5px;
  color: hsl(240, 67%, 15%);
}

.form__button:hover {
  border: 1px solid hsl(240, 67%, 40%);
  cursor: pointer;
}

.form__note {
  font-size: 0.85em;
}

.form__result {
  width: 100%;
  margin-bottom: 20px;
  border: 1px solid hsl(240, 67%, 75%);
  padding: 5px;
  border-radius: 5px;
  color: hsl(240, 67%, 15%);
  text-align: center;
}

.form__labelText {
  width: 150px;
  display: inline-block;
  margin-right: 5px;
}

.form__legend {
  width: 90%;
  border: 1px solid hsl(240, 67%, 75%);
  padding: 5px;
  border-radius: 5px;
  color: hsl(240, 67%, 15%);
  text-align: center;
}
